import { Component, OnInit, Input, ViewEncapsulation, OnChanges, SimpleChanges} from '@angular/core';

import 'easy-pie-chart/dist/jquery.easypiechart.js';

@Component({
  // moduleId: module.id,
  selector: 'rpi-info',
  templateUrl: './info.html',
  styleUrls: ['./info.scss'],
  styles: [`
    .percent {
      position: absolute !important;
      left: 53px !important;
      top: 33px !important;
    }
  `],
  encapsulation: ViewEncapsulation.None,

})
export class RpiInfoComponent implements OnChanges{
  constructor() {
  }

  @Input()
  public rpi: any;

  public charts: Array<Object> = [
    {
      color: '#555',
      variable: 'cpuLoad',
      label: 'raspberrypi.info.cpu_load',
      icon: 'person',
    }, {
      color: '#555',
      variable: 'cpuTemp',
      label: 'raspberrypi.info.cpu_temp',
      stats: '$ 89,745',
      icon: 'money',
    }, {
      color: '#555',
      variable: 'memoryUsed',
      label: 'raspberrypi.info.mem_used',
      stats: '178,391',
      icon: 'face',
    }
  ];
  private _init = false;


  ngAfterViewInit() {
    if (!this._init) {
      this._loadPieCharts();
      // this._updatePieCharts();
      this._init = true;
    }
  }

  ngOnChanges(changes: any) {
    this._updatePieCharts(changes.rpi.currentValue);
  }

  private _loadPieCharts() {

    jQuery('.chart').each(function () {
      let chart = jQuery(this);
      chart.easyPieChart({
        easing: 'easeOutBounce',
        onStep: function (from, to, percent) {
          jQuery(this.el).find('.percent').text(Math.round(percent));
        },
        barColor: jQuery(this).attr('data-rel'),
        trackColor: 'rgba(0,0,0,0)',
        size: 84,
        scaleLength: 0,
        animation: 2000,
        lineWidth: 9,
        lineCap: 'round',
      });
    });
  }

  private _updatePieCharts(rpi) {
    if (rpi == undefined ) return;

    for (var chart of this.charts) {
      if (rpi[chart['variable']] !== undefined ) {

        try {
          jQuery('.pie-charts .chart#' + chart['variable']).data('easyPieChart').update(rpi[chart['variable']]);
        } catch (e) {

        }
        
      };

    }


  }

}



