import { Component } from '@angular/core';
import { NgZone } from '@angular/core';
import { ViewEncapsulation } from '@angular/core';

import { MessengerService } from '../../messenger.service';

@Component({
  selector: 'raspberrypi',
  templateUrl: './raspberrypi.html',

  encapsulation: ViewEncapsulation.None,

})
export class RaspberryPiComponent {

  data: any;
  public cmdParentCallback: Function;

  constructor(private _messengerService: MessengerService, private zone: NgZone) {
  }

  ngOnInit() {
    this.cmdParentCallback = this.theCallback.bind(this);

    this.data = this._messengerService.getAll();
    this._messengerService.initChrome();
  }

  public theCallback(message) {
    this._messengerService.sendCmd(message);
  }



  getResponsive(padding, offset) {
    return this._messengerService.getResponsive(padding, offset);
  }

  applyData(): void {
    this.zone.run(() => { });
  }


  handleReboot() {
    this.theCallback('rpiReboot');
  }

  handleShutdown() {
    this.theCallback('rpiReboot');
  }

}



