import { Routes, RouterModule } from '@angular/router';

import { RaspberryPiComponent } from './raspberrypi.component';

const routes: Routes = [
  {
    path: '',
    component: RaspberryPiComponent
  }
];

export const routing = RouterModule.forChild(routes);



