///<reference path='./chrome/chrome-app.d.ts' />


import {Injectable} from '@angular/core';
import {NgZone} from '@angular/core';

import {BaThemeConfigProvider} from './theme';


@Injectable()
export class MessengerService {

  public defaultDatas: any = {
    sensors: [0, 0, 0, 0, 0, 0, 0, 0],
    motors: [
      { isOn: 0, direction: 'cw', isActive: 0, power: 0 },
      { isOn: 0, direction: 'cw', isActive: 0, power: 0 },
      { isOn: 0, direction: 'cw', isActive: 0, power: 0 },
      { isOn: 0, direction: 'cw', isActive: 0, power: 0 },
    ],
    info: {boardType:0, boardVersion:0, firmwareVersion:0},
    rtc : {year:0,month:0,date:0,hour:0,minute:0,second:0},
    rpi : {
        revision: 0,
        ipAddress: '0.0.0.0',
        wlanIpAddress: '0.0.0.0',
        cpuLoad: 0,
        cpuTemp: 0,
        memoryUsed: 0,
        wifiStatusCode: 0,
        emailStatusCode: 0,
        smsStatusCode: 0,
        cameraStat: 0,
        cameraActive: false,
        cameraFindFaceActive: false,
        cameraIsFoundFace: false,
        key: '',
        value: ''
    },
    irValue : 0,
  };
  // public defaultChartSensors: any = [
  //   {
  //     label: 'Sensor Values',
  //     data: [
  //       0, 0, 0, 0, 0, 0, 0, 0
  //     ],
  //     borderWidth: 1
  //   },
  // ];
  private _data = Object.assign(this.defaultDatas, {sensors: [
    {
      label: 'Sensor Values',
      data: [
        0, 0, 0, 0, 0, 0, 0, 0
      ],
      borderWidth: 1
    },
  ]});
  public datas: any = Object.assign({}, this.defaultDatas);

  public extMessenger: any = null;

  constructor(private _baConfig: BaThemeConfigProvider, private zone: NgZone) {
  }

  public ngOnInit() {
    this.resetData();
    this.initChrome();
  }

  applyData(): void {
    this.zone.run(() => { });
  }

  public updateSensors(sensorDatas: any): void {
    let clone = this.cloneData(this._data.sensors);
    if (!clone || !clone[0]) return;
    clone[0].data = sensorDatas;
    this._data.sensors = clone;
  }

  public cloneData(data: any): any {
    return JSON.parse(JSON.stringify(data));
    //return Object.assign({}, data);
  }

  public getRandomInt(min, max) {
    return Math.floor(Math.random() * (max - min + 1)) + min;
  }

  public randomSensors() {
    console.log('random Data');
    var list = [];
    for (var i = 0; i < 8; i++) {
      list.push(this.getRandomInt(0, 1024));
    }
    this.updateSensors(list);
    // this.applyData();
  }

  public getAll() {
    return this._data;
  }

  public initChrome() {

    if (this.extMessenger != null) return;

    // this.extMessenger = chrome.runtime.connect('bijgcikijffbacedkgpdpeiafcklinel')
    this.extMessenger = chrome.runtime.connect('onipnjeomppihdnpnhkffennhafpbkcg')

    this.extMessenger.onMessage.addListener(
      /* function(request, sender, sendResponse) {
        console.log(sender.tab ?
                  'from a content script:' + sender.tab.url :
                  'from the extension');
        console.log(request);
        this.updateData(request);

      } */


      (request, sender, sendResponse) => {
        // console.log(request);
        // this.datas = request;
        this.updateData(request);
        // console.log(this.data);
      }
    );

    this.extMessenger.onDisconnect.addListener((object) => {

      this.extMessenger = null;
      console.log('extMessenger Disconnect');
      // this.extMessenger = 0;
      console.log(object);
      this.resetData();

      setTimeout(this.initChrome(), 1500);
    });
  }

  sendMessage(message) {
    if (!this.extMessenger) { return; };
    this.extMessenger.postMessage(message);
  }

  sendCmd(message) {
    // this.sendMessage({cmd:'toggleMotor', param:0})
    this.sendMessage('command::' + message);
  }

  sendLogo(message) {
    this.sendMessage('logo::' + message);
  }

  resetData(): void {
    console.log('this Is resetting');
    this.datas = this.cloneData(this.defaultDatas);
    this._data.motors = this.defaultDatas.motors;
    this._data.info = this.defaultDatas.info;
    this._data.irValue = this.defaultDatas.irValue;
    this._data.rpi = this.cloneData(this.defaultDatas.rpi);
    this.updateSensors(this.defaultDatas.sensors);
    this.applyData();
  }

  updateData(datas: any): void {
    if (datas == 'nodata') {
      this.resetData();
    } else {
      // this.datas = this.cloneData(datas);
      this.datas = datas;
      this._data.info = datas.info;
      this._data.motors = datas.motors;
      this._data.irValue = datas.irValue;
      this._data.rpi = datas.rpi;
    }

    this.updateSensors(this.datas.sensors);
    // this.updateSensors(this.datas.sensors);
    // this.data.datasets[0].data = datas.sensors;

    this.applyData();

  }


  public getResponsive(padding, offset) {
    return [
      ['screen and (min-width: 1550px)', {
        chartPadding: padding,
        labelOffset: offset,
        labelDirection: 'explode',
        labelInterpolationFnc: function(value) {
          return value;
        }
      }],
      ['screen and (max-width: 1200px)', {
        chartPadding: padding,
        labelOffset: offset,
        labelDirection: 'explode',
        labelInterpolationFnc: function(value) {
          return value;
        }
      }],
      ['screen and (max-width: 600px)', {
        chartPadding: 0,
        labelOffset: 0,
        labelInterpolationFnc: function(value) {
          return value[0];
        }
      }]
    ];
  }
}



