import { Component, OnInit, NgZone, Input, OnChanges, SimpleChanges, ViewEncapsulation} from '@angular/core';

@Component({
  // moduleId: module.id,
  selector: 'rpi-network',
  templateUrl: './network.html',
  styles: [`
    .full-width {
      width: 100%;
    }
  `],
  encapsulation: ViewEncapsulation.None,

})
export class RpiNetworkComponent implements OnChanges {
  constructor(private zone: NgZone) {
  }

  @Input()
  public cmdCallback: Function;

  @Input()
  public rpi: any;

  ssid = "";
  password = "";
  isShowPassword = false;

  handleConnect() {
    console.log('Connect');
    console.log(this.ssid);
    console.log(this.password);
    this.cmdCallback('rpiWifiConnect::'+ this.ssid.trim()+'::' + this.password.trim());
  }

  applyData(): void {
    this.zone.run(() => { });
  }

  ngOnChanges(changes: any) {
    // this.applyData();
  }
}



