import { NgModule }      from '@angular/core';
import { CommonModule }  from '@angular/common';
import { FormsModule } from '@angular/forms';
import { NgaModule } from '../../theme/nga.module';


import { RaspberryPiComponent } from './raspberrypi.component';
import { routing } from './raspberrypi.routing';
import { AppTranslationModule } from '../../app.translation.module';



import { MaterialModule } from '@angular/material';
import { FlexLayoutModule } from '@angular/flex-layout';
import { ChartsModule } from 'ng2-charts';
import { MdSliderModule } from '@angular/material';
// import { MdSidenavModule } from '@angular/material';
// import {MdToolbarModule} from '@angular/material';
// import {MdMenuModule} from '@angular/material';
import { MdInputModule } from '@angular/material';
import { MdIconModule } from '@angular/material';
import { MdTooltipModule } from '@angular/material';
import {MdCheckboxModule} from '@angular/material';


import { MessengerService } from '../../messenger.service';

import { RpiKeyValueComponent } from './elements/keyvalue.component';
import { RpiInfoComponent } from './elements/info.component';
import { RpiNetworkComponent } from './elements/network.component';
import { RpiHomeScreenComponent } from './elements/homescreen.component';


@NgModule({
  imports: [
    CommonModule,
    FormsModule,
    routing,
    AppTranslationModule,

    NgaModule,


    MaterialModule,
    FlexLayoutModule,
    ChartsModule,

    MdInputModule,
    MdSliderModule,
    MdTooltipModule,
    MdCheckboxModule,
    MdIconModule,
    // BrowserAnimationsModule,

  ],
  declarations: [
    RaspberryPiComponent,
    RpiKeyValueComponent,
    RpiInfoComponent,
    RpiNetworkComponent,
    RpiHomeScreenComponent,
  ],
  providers: [
    MessengerService,
  ]
})
export class RaspberryPiModule { }



